body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu",
    "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  box-sizing: border-box;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

h4 {
  margin: 0;
}

label {
  cursor: pointer;
}

.active {
  color: blue;
}

.app {
  display: flex;
}

.employees {
  flex-basis: 75%;
  margin-right: 50px;
  border-right: 2px solid #000;
}

.employees-title {
  text-align: center;
}

.employees-wrapper {
  display: flex;
  flex-wrap: wrap;
}

.employee-wrapper + .employee-wrapper {
  /* margin-left: 30px; */
  margin-bottom: 15px;
}

.employee-name {
  margin-bottom: 5px;
}

.employee-wrapper {
  min-width: 100px;
  margin-right: 20px;
  margin-left: 20px;
}

.employee-content + .employee-content {
  margin-top: 15px;
}

.btn-group {
  display: flex;
  align-items: center;
}

.not-active-wrapper,
.active-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
}

.not-active-wrapper {
  margin-right: 7px;
}
